@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#particles-container {
  background: rgb(2, 0, 36);
  background: linear-gradient(
    117deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 63%,
    rgba(0, 40, 214, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#020024",endColorstr="#0028d6",GradientType=1);
  height: 100vh;
  z-index: -1;
}
#particles-container-about {
  background: #ffff;
  height: 100vh;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
#particles-container-projects {
  background: #f7f7f7;
  height: 100vh;
  z-index: -1;
  width: 100vw;
}

body{font-family:'Montserrat', sans-serif;overflow-x:hidden;width:100%}#toggler{text-align:center;height:auto;width:80px;color:white;background-color:transparent;border:none;border-radius:10px;position:fixed;top:45%;left:2%;z-index:3;cursor:pointer;font-weight:500}#toggler span{height:2px;width:40px;background-color:#fff;display:block;margin:6px 0px 0px 20px}#menu{position:fixed;top:0;left:0;background:linear-gradient(117deg, #020024 0%, #090979 63%, #0028d6 100%);width:0%;height:100%;z-index:2}#menu ul{list-style:none;display:flex;position:absolute;top:37%;left:25%;visibility:hidden}#menu ul li{margin:40px;font-weight:500;font-size:40px;color:#fff;font-family:'Staatliches', cursive}#menu ul li .link{color:#fff;text-decoration:none}#intro{position:absolute;top:40%;left:10%;background-color:transparent;text-align:left;z-index:1;color:red}#intro h1{color:#fff}.aboutMain{position:absolute;top:0;left:0}.aboutMain h1{font-size:10px}#hire-me{margin:4.5rem 0px;position:relative}#hire-me h1{font-size:clamp(1.4em, 5vw, 2em)}#hire-me p{font-size:clamp(0.8em, 2vw, 1em)}#hire-me .skills{margin:20px 0px}#hire-me .skills #progress,#hire-me .skills #progress1,#hire-me .skills #progress2,#hire-me .skills #progress3{height:10px;width:15rem;background:linear-gradient(to right, royalblue 0%, royalblue 80%, #e4e4e4 80%, #e4e4e4 100%);position:relative;border-radius:10px;margin-top:5px}#hire-me .skills #progress::before,#hire-me .skills #progress1::before,#hire-me .skills #progress2::before,#hire-me .skills #progress3::before{content:'';display:block;height:25px;width:25px;background-color:#fff;border-radius:50%;border:6px solid royalblue;position:absolute;top:-7px;left:77%}#hire-me .skills #progress1{background:linear-gradient(to right, royalblue 0%, royalblue 84%, #e4e4e4 84%, #e4e4e4 100%)}#hire-me .skills #progress1::before{left:82%}#hire-me .skills #progress2{background:linear-gradient(to right, royalblue 0%, royalblue 60%, #e4e4e4 60%, #e4e4e4 100%)}#hire-me .skills #progress2::before{left:56%}#hire-me .skills #progress3{background:linear-gradient(to right, royalblue 0%, royalblue 84%, #e4e4e4 84%, #e4e4e4 100%)}#hire-me .skills #progress3::before{left:82%}#hire-me .skills span{font-size:12px}#hire-me .skills #level{position:absolute;left:40%;margin:4px}.experience{position:relative}.experience #header{text-align:center}.experience #header h1{font-size:clamp(1.4em, 5vw, 2em)}.experience #header::after{content:'';height:4px;width:120px;background-color:#503ada;display:block;align-self:center;margin:0px 0px 0px 30.2em}.experience .timeLine{height:22rem;width:4px;background-color:slateblue;position:absolute;left:50%;top:15%;border-radius:10px}.experience .timeLine::before{content:'1';text-align:center;font-weight:bold;display:block;height:35px;width:35px;background-color:#fff;border-radius:50%;border:5px solid royalblue;position:absolute;top:20%;left:-15px}.experience .timeLine::after{content:'2';display:block;text-align:center;font-weight:bold;height:35px;width:35px;background-color:#fff;border-radius:50%;border:6px solid royalblue;position:absolute;top:70%;left:-16px}.experience .companies{display:grid;grid-template-columns:1fr 1fr;justify-items:center;grid-auto-rows:minmax(150px auto);grid-auto-columns:minmax(150px auto);grid-row-gap:20px}.experience .companies #company1 h3{margin-top:80px}.experience .companies #company2{grid-column:2/3;grid-row:2}.experience .companies #company2 h3{margin-top:40px}.experience .companies #discription1{padding:40px 0px 0px 40px}.experience .companies #discription2{padding:0px 40px 0px 0px}#form{display:block;padding:25%}#form input{width:100%;padding:12px 20px;margin:8px 0;display:block;border:1px solid #ccc;border-radius:4px;box-sizing:border-box}#form label{font-weight:bolder}

@media only screen and (max-width: 600px){#toggler{top:4%;left:82%;transform:scale(0.8)}#menu ul{display:block;top:10%;left:0%;font-size:smaller}#menu ul li{font-size:x-large;margin:40px 0px}.aboutMain{position:absolute;top:0;left:0}#hire-me{margin:4.5rem 0px;position:relative}#hire-me .skills{margin:20px 0px}#hire-me .skills #progress,#hire-me .skills #progress1,#hire-me .skills #progress2,#hire-me .skills #progress3{height:10px;width:15rem;background:linear-gradient(to right, royalblue 0%, royalblue 80%, #e4e4e4 80%, #e4e4e4 100%);position:relative;border-radius:10px;margin-top:5px}#hire-me .skills #progress::before,#hire-me .skills #progress1::before,#hire-me .skills #progress2::before,#hire-me .skills #progress3::before{content:'';display:block;height:25px;width:25px;background-color:#fff;border-radius:50%;border:6px solid royalblue;position:absolute;top:-7px;left:77%}#hire-me .skills #progress1{background:linear-gradient(to right, royalblue 0%, royalblue 84%, #e4e4e4 84%, #e4e4e4 100%)}#hire-me .skills #progress1::before{left:82%}#hire-me .skills #progress2{background:linear-gradient(to right, royalblue 0%, royalblue 60%, #e4e4e4 60%, #e4e4e4 100%)}#hire-me .skills #progress2::before{left:56%}#hire-me .skills #progress3{background:linear-gradient(to right, royalblue 0%, royalblue 84%, #e4e4e4 84%, #e4e4e4 100%)}#hire-me .skills #progress3::before{left:82%}#hire-me .skills span{font-size:12px}#hire-me .skills #level{position:absolute;left:40%;margin:4px}.experience{position:relative}.experience #header{text-align:center}.experience #header::after{content:'';height:4px;width:120px;background-color:#503ada;display:block;align-self:center;margin:-5px 0px 0px 10.5em}.experience .timeLine{height:40.4rem;width:4px;background-color:slateblue;position:absolute;left:2%;top:6%;border-radius:10px}.experience .timeLine::before{content:'1';text-align:center;font-weight:bold;display:block;height:35px;width:35px;background-color:#fff;border-radius:50%;border:5px solid royalblue;position:absolute;top:9%;left:-15px}.experience .timeLine::after{content:'2';display:block;text-align:center;font-weight:bold;height:35px;width:35px;background-color:#fff;border-radius:50%;border:6px solid royalblue;position:absolute;top:60.5%;left:-16px}.experience .companies{display:block}.experience .companies #company1{padding:0px 20px 0px 40px}.experience .companies #company2{padding:0px 20px 0px 40px}.experience .companies #discription1{padding:0px 20px 0px 40px}.experience .companies #discription2{padding:0px 20px 0px 40px}}

